.ayat-arab {
  font-family: 'Scheherazade', serif;
  font-size: 34px;
}
.ayat-latin {
  font-weight: 300;
}
.ayat-latin, .ayat-arti {
  font-family: 'Poppins', sans-serif;
}
#navbar {
  top: 0;
  transition: top 0.3s; 
  z-index: 10;
  background: #253630;
}
.footer {
  background: #253630 !important;
  color: rgb(216, 216, 216);
  font-family: 'Poppins', sans-serif;
  font-size: 15px;
}
.font-arab {
  font-family: 'Scheherazade', serif;
  font-size: 28px;
  line-height: 0.8;
}
.paginate-bg {
  background: linear-gradient(to top left,#00C11F,#1ADA71);
  font-family: 'Poppins', sans-serif;
}
.home-item {
  font-family: 'Poppins', sans-serif;
}
.home-item .arab {
  font-family: 'Scheherazade', serif;
  font-size: 24px;
  line-height: 1;
}
.noarab-item {
  font-size: 2.8vmax;
  margin: 0;
}
.navbar-nav a, .paginate-bg a {
  color: #cde2db;
}
.navbar-nav a:hover, .paginate-bg a:hover {
  color: #e4f5f0;
}
.paginate a {
  border-radius: 10px;
  background: #115921;
  padding: 2px 4px;
}
.badge-noarab {
  background: url('./frame.svg') no-repeat;
  background-position: center center;
  padding: 0 10px;
  letter-spacing: -1px;
}

 /* The side navigation menu */
 .sidenav {
  height: 100%; /* 100% Full-height */
  width: 280px; /* 0 width - change this with JavaScript */
  position: fixed; /* Stay in place */
  z-index: 11; /* Stay on top */
  top: 0; /* Stay at the top */
  /* left: 0; */
  background-color: #253630; /* Black*/
  overflow-x: hidden; /* Disable horizontal scroll */
  /* padding-top: 60px; Place content 60px from the top */
  transition: 0.5s; /* 0.5 second transition effect to slide in the sidenav */
  overflow-y: scroll;
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.sidenav::-webkit-scrollbar {
  display: none;
}

/* The navigation menu links */
.sidenav a {
  padding: 8px 8px 8px 32px;
  text-decoration: none;
  font-size: 25px;
  color: rgb(216, 216, 216);
  font-family: 'Scheherazade', serif;
  display: block;
  transition: 0.3s;
}
.active {
    background: #00C11F;
}
.sidenav small {
  font-family: 'Poppins', sans-serif;
  font-size: 12px;
}

/* When you mouse over the navigation links, change their color */
.sidenav a:hover {
  color: #f1f1f1;
}

/* Position and style the close button (top right corner) */
.sidenav .closebtn {
  position: sticky;
  position: -webkit-sticky;
  top: 0;
  right: 10px;
  font-size: 36px;
  color: #fff;
}

/* Style page content - use this if you want to push the page content to the right when you open the side navigation */
#main {
  transition: margin-left .5s;
  padding: 20px;
}

/* On smaller screens, where height is less than 450px, change the style of the sidenav (less padding and a smaller font size) */
@media screen and (max-height: 450px) {
  .sidenav {padding-top: 15px;}
  .sidenav a {font-size: 18px;}
} 
.list-ayat {
  margin-top: 12vh;
}
.custom-switch > .custom-control-label {
  color: rgb(216, 216, 216);
  font-family: 'Poppins', sans-serif;
  font-size: 15px;
}

